* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Poppins", sans-serif;
  background-color: rgba(255, 255, 255, 0.562);
}
.maincontainer {
  height: auto;
  width: 80%;
  display: flex;
  margin: 3% auto;
  background-color: white;
}
.logincontainer {
  padding: 10px;
  width: 50%;
  height: 80vh;
}
.logo {
  display: flex;
  align-items: center;
  justify-content: center;
}
.logo img {
  height: 65px;
  width: 305px;
  margin: 10px auto;
}
.Login {
  font-size: 40px;
  color: #192a3e;
  margin: 10px 0 30px 80px;
}

.loginform {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
}
.loginform input[type="email"],
.loginform input[type="password"] {
  border-radius: 4px;
  border: 1px solid rgba(128, 128, 128, 0.541);
  width: 350px;
  font-size: 14px;
  color: black;
  background: #cae6f7a1;
  padding: 10px;
  margin-bottom: 15px;
}
.checkbox_rmbr {
  padding: 4px;
  display: flex;
  width: 60%;
  align-items: center;
  justify-content: space-between;
  font-size: 15px;
}
.rmbr {
  padding-left: 5px;
  color: #000000;
}
.rmbrme {
  display: flex;
  align-items: center;
}
.checkbox_rmbr a {
  color: #000000;

  text-decoration: none;
}

.LOGINBTN {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 15px 0;
}
.loginbtn {
  padding: 8px;
  width: 180px;
  height: 44px;
  background: #109cf1;
  color: whitesmoke;
  font-size: 20px;
  border: none;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  margin: 10px 0;
}
.loginbtn:hover {
  background-color: #cae6f7a1;
  color: #109cf1;
}
.accntcrt {
  display: none;
}
