.or {
  margin: 15px auto;
  width: 80%;
  text-align: center;
  border-bottom: 1px solid rgba(151, 149, 149, 0.774);
  font-size: 12px;
  line-height: 0.1em;
}
.or span {
  background: #fff;
  padding: 0 10px;
}
.cnct_p {
  display: none;
}
.social {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 5px;
}
.social button {
  padding: 7px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 120px;
  background-color: #109cf1;
  border: none;
  font-family: "Poppins", sans-serif;
  border-radius: 4px;
  font-size: 15px;
  color: white;
  cursor: pointer;
}
.fa-google,
.fa-facebook-square,
.fa-linkedin {
  padding: 4px;
  font-size: 20px;
}
.fa-google {
  background: linear-gradient(to right, #ea4335, #fbbc05, #34a853, #4285f4);
}
.fa-facebook-square {
  background-color: #0067a7;
}
.fa-linkedin {
  background-color: #234f81;
}
.policy {
  margin: 15px;
  text-align: center;
  color: rgb(75, 75, 75);
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  line-height: 18px;
}
.policy a {
  color: #109cf1;
  text-decoration: none;
}
.help {
  margin-bottom: 5px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  text-align: center;
}
.help a {
  color: #1e2938;
  text-decoration: none;
}
.welcomecontainer {
  background: linear-gradient(320deg, #008bdf -2%, #32b2ff 100%);
  width: 50%;
  border-radius: 10px;
  height: auto;
}
.Welcome_sh {
  margin-top: -180px;
}
.about {
  display: flex;
  flex-direction: column;
  margin-left: 60px;
}
.about h1 {
  font-size: 35px;
  color: white;
}
h1 span {
  font-size: 45px;
  color: white;
}
.about p {
  width: 80%;
  margin: 25px 0;
  color: white;
}
.accountbtn {
  background-color: white;
  padding: 10px;
  border: none;
  font-size: 16px;
  font-weight: 700;
  border-radius: 4px;
  color: #109cf1;
  cursor: pointer;
}
.accountbtn:hover {
  color: #fff;
  background-color: #109cf1;
}
.broadcast {
  line-height: 30px;
  font-size: 19px;
  width: 50%;
  margin: 20px 0;
  font-weight: 500;
  color: white;
}
.mail,
.mail a {
  color: white;
  text-decoration: none;
  font-size: 13px;
}
.mail {
  margin-top: -40px;
}
.phone {
  margin-top: 10px;
}
.mail .fa-envelope {
  padding-right: 8px;
}
.phone {
  color: white;
  font-size: 12px;
}
.phone .fa-phone-alt {
  padding-right: 12px;
}
.Brdcst_dot {
  display: flex;
  justify-content: space-between;
}
.ellipse1 {
  position: relative;
  width: 40px;
  height: 40px;
  top: 9%;
  left: 17%;
  border-radius: 50%;
  border: 8px solid rgba(255, 255, 255, 0.2);
}
.ellipse2 {
  position: relative;
  width: 65px;
  height: 65px;
  top: 125px;
  right: 15%;
  border-radius: 50%;
  border: 10px solid rgba(255, 255, 255, 0.2);
}
.ellipse3 {
  position: relative;
  width: 220px;
  height: 220px;
  top: -25%;
  left: 80%;
  border-radius: 50%;

  border: 30px solid rgba(255, 255, 255, 0.5);
}
.dots {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  display: inline-block;
  padding-left: 17px;
  background: linear-gradient(
    215.57deg,
    rgba(255, 255, 255, 0.65) 14%,
    rgba(255, 255, 255, 0) 96%
  );
  transform: rotate(-90deg);
}
.dotcontainer {
  display: flex;
  align-items: flex-end;
  flex-direction: column;
}
