/* Main Page */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  color: #334d6e;
  font-family: "Poppins", sans-serif;
  height: auto;
}
.Mainjob {
  background-color: white;
  height: 100vh;
  width: 100%;
  display: flex;
  position: relative;
}
.logobox_Main {
  width: 22%;
  height: 100vh;
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  align-items: center;
  background-color: whitesmoke;
  flex-direction: column;
  box-shadow: 0 0 4px;
}
.logo {
  margin: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70px;
  background-color: #fff;
}
.user_Main {
  margin: 20px;
}
.user_Main h3,
.user_Main p {
  margin: 0;
}
.Aj_Dj_Ej {
  padding-bottom: 5px;
}
.NavContainer_Main {
  width: 78%;
  height: auto;
  position: absolute;
  left: 22%;
}
.navbar_Main {
  height: 50px;
  width: 77.8%;
  position: fixed;
  left: 22.2%;
  z-index: 1;
  display: flex;
  box-shadow: 0 1px 4px grey;
  background-color: whitesmoke;
  align-items: center;
  justify-content: flex-end;
}
.list {
  display: flex;
}

.list li {
  margin: 0 30px 0 10px;
  list-style: none;
}
.list li a {
  text-decoration: none;
  color: #192a3e;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.list li a .fas,
.list li a .far {
  padding-right: 5px;
  color: #008bdf;
}

.whole {
  width: 100%;
  position: absolute;
  top: 58px;
}

.Main_btns {
  margin: 10px 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.job_intrv_btns {
